@import url(https://fonts.googleapis.com/css?family=Arima+Madurai:300);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#wrapper {
  margin-left:auto;
  margin-right:auto;
}
.App {
  width: fixed;
  height: fixed;
  text-align: center;
  background-color: #F7F4F2;
  overflow: hidden;
  font-family: Georgia, 'Times New Roman', Times, serif;
  color: #323231;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a:link {
  color: #323231;
}

a:active {
  color: #323231;
}

a:visited {
  color: #323231;
}

.projectheader {
  text-transform: uppercase;
  font-family: Georgia, 'Times New Roman', Times, serif;
  padding-top: 20px;
}

@media only screen and (max-width: 600px){

    .animated-text {
      font-size: 100%;
    }

    h1.projectheader {
      color: #323231;
    }
  }

.About {
  margin-top: 15%;
  margin-left: 20%;
  margin-right: 20%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin-bottom: 15%;
}

img.About-img {
  width: 80%;
  height: auto;
  margin-top: 15%;
}

.container {
  margin-left: 33%;
  position: relative;
  height: 300px;
  width: 260px;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: 900ms;
  background-color: #F7F4F2;
}

.container:hover .overlay {
  opacity: 1;
}

.overlay-text {
  color: #323231;
  font-size: 15px;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}



.About-text {
  text-align: left;
  font-family: 'Trirong', serif;
}

.About-title {
  text-transform: uppercase;
  font-family: Georgia, 'Times New Roman', Times, serif;
  text-align: center;
}


@media only screen and (max-width: 600px){

    .About {
      width: 100%;
      margin-bottom: 110%;
      margin-left: 1%;
    }
  
    .About-text {
      font-size: 5.5vw;
      white-space: normal;
      margin-right: 2%;
      margin-left: 2%;
    }

    .About-title {
      display: none;
    }

    .container {
     display: none;
    }
  }

.Contact {
    padding-top: 300px;
    padding-bottom: 50px;
    text-align: center;
    color: #323231;
    margin-bottom: -50px;
    background-image: linear-gradient(#CCBEB8 20%, #F7F4F2);
    -webkit-clip-path: polygon(0 20%, 100% 36%, 100% 100%, 0% 100%);
            clip-path: polygon(0 20%, 100% 36%, 100% 100%, 0% 100%); 
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: 'Trirong', serif;

}

.animated {
    font-family: Georgia, 'Times New Roman', Times, serif;
}
 
p.footer {
    font-size: 12px;
}
    
@media only screen and (max-width: 600px){

    .Contact {
        padding-top: 250px;
        font-size: 4.5vw;
    }

    .animated {
        font-size: 6vw;
      }
  
  }


.Project-card {
    background-color: #F7F4F2; 
    width: 30%;
    height: auto;
    display: inline-block;
    padding: 7px;
    color: #323231;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: 'Trirong', serif;
}

.image {
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    transition: 700ms;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    border: 3px solid #323231;
  }

.text {
    transition: 700ms;
    opacity: 0;
    position: absolute;
    margin-left: 15%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
  }
  
  .Project-card:hover .image {
    opacity: 0.3;
  }
  
  .Project-card:hover .text {
    opacity: 1;
  }
  
  .text {
    background-color: #ebb7a8cb;
    padding: 16px 32px;
    border-radius: 2px;
  }

a.live:hover {
  color: #62B9C9;
}

@media only screen and (max-width: 600px){

    .Project {
      width: 100%;
      height: auto;
    }

    .Project-card {
       width: 90%;
       height: auto;
       padding-bottom: 50px;
       font-size: 5vw;
    }

    .Project:hover {
        transform: scale(1);
        transition: 900ms;
        }
  
  }
*,
*::before,
*::after {
  box-sizing: border-box;
}

:root{
    --bg-color: linear-gradient(#E5DEDB, #DAD0CB);
}

.home-title {
    text-align: center;
    color: #323231;
    background-image: linear-gradient(#F7F4F2, #CCBEB8 70%);
    background-size: cover;
    position: relative;
    height: 350px;
    -webkit-clip-path: polygon(
      0 0,
      200% 0,
      200% 110%,
      0 calc(100% - 130px)
    );
            clip-path: polygon(
      0 0,
      200% 0,
      200% 110%,
      0 calc(100% - 130px)
    );
}

.Nav-bar {
  font-size: large;
  font-family: 'Trirong', serif;
}


.home-title{
  font-size: 55px;
  font-family: 'Cinzel', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


